<!--  -->
<template>
    <div class="ui tree-view">
        <tree-view-recursion :dataArr="getDataArr" :setConfig="options"></tree-view-recursion>
    </div>
</template>

<script scoped>
    export default {
        props: {
            dataArr: {
                default: function () {
                    return []
                }
            }, // json
            setConfig: {
                default: function () {
                    return {
                        isShow: 1, //  1-2-
                        isShowIcon: 1, //  1-2-
                        showChoice: false, // 
                        isActive: false, // 
                        choiceConfig: {
                            choiceChild: true, // 
                        }, // 
                        keyConfig: {
                            name: 'name',
                            id: 'id',
                            childKey: 'childList',
                        }, // 
//                        btn: [{
//                            iconFontClass: '', // class
//                            color: '', // 
//                            handle: '' // 
//                        }], // 
//                        locationConfig: {
//                            key: '', // 
//                        }, // 
                    }
                }
            }
        },
        data() {
            return {
                getDataArr: this.dataArr,
                options: this.setConfig
            }
        },
        watch: {
            getDataArr: {
                handler: function (val, oldval) {
                    this.$emit('dataArrChange', val);
                },
                deep: true
            },
            dataArr: {
                handler: function (val) {
                    this.getDataArr = val;
                },
                immediate: true,
                deep: true
            },
            setConfig: {
                handler: function (val) {
                    this.options = val;
                },
                immediate: true,
                deep: true
            }
        },
        methods: {
            // 
            clickBtn: function (handle,item,index) {
                let self = this;
                this.$emit(handle,item,index);
            }
        },
    }
</script>

<style lang="scss" scoped>
    .ui.tree-view {
        background-color: #fff;
        & > ul {
            padding-left: 15px;
        }
        .fade-enter-active {
            transition: opacity .3s;
        }
        .fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */
        {
            opacity: 0;
        }
    }
</style>



